.wrapper{
    position: relative;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}
.details{
    z-index: 100;
    position: relative;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    min-width: 200px;
    transition: linear 0.1s;
}
.track-name{
    cursor: pointer;
    font-size: 16px;
}
.track-name:hover {
    text-decoration-line: underline;
    
}
.track-artist:hover {
    text-decoration-line: underline;
}
.track-artist{
    cursor: pointer;
    margin-top: 5px;
    font-size: 14px;
}
.buttons{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.active{
    color: black;
}
.repeat-track,
.random-track,
.playpause-track,
.prev-track,
.next-track{
    padding: 0 10px;
    opacity: 0.8;
    transition: opacity .2s;
}
.repeat-track:hover,
.random-track:hover,
.playpause-track:hover,
.prev-track:hover,
.next-track:hover{
    opacity: 1.0;
}
.slider_container{
    display: flex;
    justify-content: center;
    align-items: center;
}
.seek_slider,
.volume_slider{
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    height: 2px;
    background: #83a9ff;
    -webkit-transition: .2s;
    transition: opacity .2s;
    
}
.track-name, .track-artist {
    margin-left: 10px;
}

.seek_slider::-webkit-slider-thumb,
.volume_slider::-webkit-slider-thumb{
    outline: none;
    border: none;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    width: 10px;
    height: 10px;
    background: white;
    border: 3px solid #3d3d3d;
    cursor: pointer;
    border-radius: 100%;
}
.seek_slider:hover,
.volume_slider:hover{
    opacity: 1.0;
}
.seek_slider{
    background-color: white;
    width: 90%;
}
.volume_slider{
    background-color: #3d3d3d;
    border-radius: 4px;
    width: 100px;
}
.current-time,
.total-duration{
    padding: 10px;
    color: #8758FF;
}

i,
i.fa-play-circle,
i.fa-pause-circle,
i.fa-step-forward,
i.fa-step-backward{
    cursor: pointer;
    font-size: 40px;
    color: white;
}
.other_block_btn i {
    color: #8758FF;
    font-size: 50px;
}
.randomActive{
    color: black;
}

.btns_container {
    display: inherit;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    column-gap: auto;
}

.rotate{
    animation: rotation 8s infinite linear;
}
.loader{
    height: 70px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.loader .stroke{
    background: #f106f1;
    height: 150%;
    width: 10px;
    border-radius: 50px;
    margin: 0px 5px;
    animation: animate 1.4s linear infinite;
}
.info_btn {
    width: 80px;
    height: 80px;
    filter: invert(100%) sepia(100%) saturate(1%) hue-rotate(305deg) brightness(103%) contrast(101%);

}
.prev-track {
    transform: rotate(180deg);
}
.prev-track, .next-track, .repeat-track, .random-track  {
    width: 20px;
    height: 20px;
}

.music_thumnail_img {
    width: 60px;
    height: 60px;
}
.details_inner {
    position: relative;
    height: 98%;
    transition: 1s;
    display: inherit;
    width: 94%;
    margin: auto auto;
    border-radius: 5px;
    background-color: #8758FF;  
}


.track-info  {
    display: flex;
    justify-content: center;
    color: white;
    position: relative;
}
.playerlikedbtn {
    width: 38px;
    position: absolute;
    right: 20px;
    filter: invert(100%) sepia(100%) saturate(1%) hue-rotate(305deg) brightness(103%) contrast(101%);
    display: none;
}
#main_info701 {
    width: 100%;
}

.slider_container_time {
    height: 24px;
    width: 500px;
}
#main_info701 {
        display: none;
    }
    .track-name, .track-artist {
        margin-left: 0px;
    }
    .music_thumnail_img {
        width: 46px;
        height: 46px;
        position: fixed;
        transition: linear 0.1s;
    }
    .playpause-track > i {
        font-size: 60px;
    }
    .buttons {
        row-gap: 20px;
    }
    .slider_container_volume {
        display: none;
    }
    .prev-track, .next-track, .repeat-track, .random-track  {
        width: 28px;
        height: 28px;
    }
    i,
    i.fa-play-circle,
    i.fa-pause-circle,
    i.fa-step-forward,
    i.fa-step-backward{
    cursor: pointer;
    color: white;
}
    .details {
        position: fixed;
        bottom: 50px;
        width: 100%;
    }
    .current-time, .total-duration {
        color: white;
    }
    .current-time {
        transform: translate(70%, -50%);
    } 
    .total-duration {
        transform: translate(-70%, -50%);
    }
    .slider_container_time {
        width: 90%;
        left: -20px;
    }

.details-open {
    height: 100%;
    bottom: 0;
}
.details-close {
    height: 50px;
}

.details-inner-open {
    flex-direction: column;
}
.music_thumnail_img_open {
    width: 90%;
    height: 90%;
    max-width: 280px;
    max-height: 280px;
    padding-bottom: 30px;
}

.details-inner-img-container  {
    display: flex;
    justify-content: center;
    justify-items: center;
    min-width: 70px;
}

.details-inner-img-container > div {
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
}
.details-inner-img-container-open {
    width: 100%;
    max-height: 360px;
    aspect-ratio: 1/1;
}

.upbtns {
    position: relative;
    width: 100%;
    height: 40px;
    display: flex;
    justify-content: space-between;
}
.arrow-player {
    height: fit-content;
    width: 60px;
    transition: 1s;
    z-index: 120;
    display: block;
}
.arrow-player > img {
    margin-top: 14px;
    margin-left: 14px;
    height: 24px;
    width: 24px;
    transform: rotate(180deg);
}


.track-info-open {
    justify-content: left;
    justify-items: left;
    padding-bottom: 50px;
}

.track-name-open {
    font-size: 24px;
    margin-left: 20px;
}
.track-artist-open {
    font-size: 18px;
    margin-left: 20px;
}
#main_info {
    width: 100%;
}

.main-info-hidden {
    display: none;
    visibility: collapse;
}
#player_bar {
    height: 100px;
}

.clickareaclose {
    position: absolute;
    bottom: 30px;
    width: 100%;
    height: 50px;
    z-index: 110;
}
.action-player {
    width: 24px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 10px;
}
.play-pause-btn {
    position: absolute;
    display: flex;
    text-align: center;
    align-items: center;
    right: 14px;
    height: 100%;
}
.play-pause-btn i {
    font-size: 20px;

}



