body {
    font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  
}
.playlist_info {
    width: 100%; 
}
.info_container {
    cursor: pointer;
    display: flex;
    flex-direction: row;
    column-gap: 30px;
    margin-top: 35px;
    margin-left: 35px;
}
.playlist_img {
    width: 232px;
    min-width: 232px;
    height: 232px;
}
.playlist_info_name {
    font-size: 6rem;
    font-weight: 1000;
    white-space: nowrap;
}

.playlist_info_type {
    margin-bottom: 0px;
    margin-top: 30px;
    font-weight: 1000;
}
.act_area {
    height: 100px;
}
.btn_list {
    display: flex;
    flex-direction: row;
    column-gap: 24px;
    margin-top: 10px;
    margin-left: 35px;
    align-items: center;
}
.act_btn_playlist {
    width: 56px;
    height: 56px;
    margin: auto 0 ;
    position: relative;
}
.likedbtn {
    width: 40px;
    height: 40px;
    filter: invert(100%) sepia(100%) saturate(1%) hue-rotate(305deg) brightness(103%) contrast(101%);
    transition: 0.5s;
}
.editbtn {
    width: 30px;
    height: 30px;
}
.track-list {
    display: flex;
    row-gap: 2px;
    flex-direction: column;
}
.track-bar {
    height: 50px;
    display: flex;
    flex-direction: row;
}
.track-block {
    position: relative;
    margin-left: 35px;
    height: 50px;
    display: flex;
    flex-direction: row;
    transition: 0.3s;
    border-radius: 4px;
    align-items: center;
}
.trackindex {
    position: absolute;
    width: 54px;
    text-align: center;
    font-size: 15px;
    font-weight: 600;
    aspect-ratio: 1/1;
    transition: all 0.3s;
}
.playtrackbtn {
    cursor: pointer;
    transition: all 0.3s;
    visibility: hidden;
    opacity: 0;
    right: 0;
    top: 0;
    position: absolute;
    width: 100%;
    height: 100%;
    filter: invert(100%) sepia(100%) saturate(1%) hue-rotate(305deg) brightness(103%) contrast(101%);

}
.playtrackbtn img {
    width: 22px;
    aspect-ratio: 1/1;
}
.track-block:hover .playtrackbtn{
    visibility: visible;
    opacity: 1;
}
.track-block:hover .musindex > p {

    opacity: 0;
}
.playtrackbtn_img {
    margin-top: 10px;
    width: 40px;
    height: 40px;
}

i.collection_btn {
    font-size: 60px;
}
.thumnail {
    height: 44px;
    margin-right: 14px;
    aspect-ratio: 1/1;
}
.track-name {
    font-weight: 500;
}
.playlist_info_authors {
    margin: 0;
    margin-top: 8px;
    font-size: 14px;
    color: rgb(207, 207, 207);
}
.track-name, .track-author {
    cursor: pointer;
    font-size: 16px;
    margin: 0;
}
.track-name:hover {
    text-decoration-line: underline;
    
}
.track-author:hover {
    text-decoration-line: underline;
}
.track-author {
    margin-top: 5px;
}

.artist-block {
    display: flex;
    flex-direction: row;
    column-gap: 14px;
}
.artist-img {
    margin-left: 14px;
    width: 70px;
    height: 70px;
    border-radius: 50%;
    background-color: black;
}
.artist-img > img {
    border-radius: 50%;
}
.artist-info {
    display: flex;
    justify-content: center;
    align-items: center;
}


.artist-info > div > p {
    margin: 0;
}
.artist-info-type {
    font-size: 16px;
}
.artist-name {
    font-weight: 500;
    font-size: 18px;
}

.editbtn > img {
    filter: invert(73%) sepia(59%) saturate(5011%) hue-rotate(232deg) brightness(101%) contrast(101%);
}
.actionbtncollection {
    width: 24px;
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    right: 14px;
    transition: 0.2s;
}
.actionbtncollection img {
    filter: invert(100%) sepia(0%) saturate(0%) hue-rotate(330deg) brightness(106%) contrast(104%);
}
.track-block:hover .actionbtncollection {
    opacity: 1;
}

.info_container {
        flex-direction: column;
        margin: 0;
    }
    .playlist_info_block > p, h1 {
        margin-left: 14px;
    }
    .act_area {
        height: 80px
    }
    .playlist_img > img {
        width: 220px;
        height: 220px;
        margin-top: 10px;
    }
    .btn_list {
        position: relative;
        flex-direction: row-reverse;
        margin-left: 0;
        margin-right: 14px;
    }
    .playlist_info {
        padding-top: 20px;
    }
    .track-list {
        padding-bottom: 140px;
        row-gap: 8px;
    }
    .likedbtn {
        position: absolute;
        top: 10px;
        left: 14px;
    }
    .track-block {
        margin-left: 14px;
    }
    .playlist_info_type {
        display: none;
    }
    .playlist_info_name {
        font-size: 20px;
    }
    .playlist_img {
        height: 240px;
        display: flex;
        margin: 0 auto;
        justify-content: center;
    }
    .playlist_author_length {
        display: none;
    }
    .musindex {
        position: absolute;
    }
    .thumnail {
        width: 54px;
        height: 54px;
    }
    .trackindexnum {
        display: none;
    }
    .playtrackbtn {
        display: flex;
        justify-content: center;
        align-items: center;
        left: 0;
    }
    .playtrackbtn img {
        width: 40px;
        height: 40px;
    }


.liked-btn-block {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    right: 60px;
    height: 100%;
    transition: 0.2s;
}
.liked-btn-block img {
    width: 30px;
    height: 30px;
}
.follow-btn  p {
    cursor: pointer;
    padding: 10px 20px;
    border: solid 2px black;
    border-radius: 4px;
    letter-spacing: 2px;
    font-weight: 600;
    transition: 0.3s;
    font-size: 14px;
}
.follow-btn  p:hover {
    border: solid 2px rgb(255, 255, 255);
    /* background-color: rgba(255, 255, 255, 0.221); */
}
