.section_block_library {
    grid-template-columns: repeat(auto-fill, minmax(155px, 180px));
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(1, auto);
    height: auto;
    row-gap: 5px;
    column-gap: 5px;
}

.container_block_collection > .block_name {
    font-size: 30px;
    position: relative;
}

.container_block_collection > .block_name > p {
    margin: 0;
}
.bla_bla p {
    margin: 0;
    margin-bottom: 10px;
}
.up-container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}
.cre-play-btn {
    display: flex;
    justify-content: center;
    align-items: center;
}
.cre-play-btn img {
    margin-right: 10px;
    margin-top: 10px;
    width: 40px;
    height: 40px;
    filter: invert(100%) sepia(100%) saturate(1%) hue-rotate(305deg) brightness(103%) contrast(101%);
}
.collection-list {
    display: flex;
    flex-direction: row;
    column-gap: 10px;
    margin-left: 20px;
    padding-top: 20px;
}
.collection-list div p {
    margin: 0;
    font-size: 17px;
    padding: 6px 6px;
    transition: 0.3s;
    cursor: pointer;
    font-size: 16px;
}
.collection-list div p:hover {
    background-color: rgb(127, 76, 255);
}
.container_block_collection {
    padding-top: 0;
    padding-bottom: 150px;
}
